import React, {useState} from 'react';
import { Link } from 'react-router-dom';
import logo from "../images/logo.png";

function Imprissum(props) {
    const [nav, setNav] = useState(false);

    const changeBg = () => {
      if (window.scrollY >= 50) {
        setNav(true);
      } else {
        setNav(false);
      }
    };
    window.addEventListener("scroll", changeBg)
    return (
       <>
       <nav className={nav ? "active" : " "}>
      <img src={logo} alt="logo" className="logo"></img>
      <ul>
        <li><Link to ="/">Return to main</Link></li>
      </ul>
    </nav>
    <div className='impriss-box'>
      <h3>Nota prawna</h3>
      <p>Dane identyfikacyjne firmy:</p>
      <p>
        Tricity Solution Spka z o.o. z siedzib w Gdasku przy ul. Lborskiej 3B 
        (80-386), zarejestrowana w rejestrze stowarzysze, innych organizacji 
        spoecznych i zawodowych, fundacji oraz samodzielnych publicznych zakadw
         opieki zdrowotnej Krajowego Rejestru Sdowego prowadzonym przez Sd 
         Rejonowy Gdask Pnoc w Gdasku, VII Wydzia Gospodarczy Krajowego 
         Rejestru Sdowego, pod numerem 0000951189, numer NIP: 5842813640, numer
          REGON: 521207574.
      </p>
    </div>
       </>
    );
}
export default Imprissum;