import React, { useState } from "react";
import logo from "../images/logo.png";
import { Link } from "react-router-dom";

function Rodo(props) {
  const [nav, setNav] = useState(false);

  const changeBg = () => {
    if (window.scrollY >= 50) {
      setNav(true);
    } else {
      setNav(false);
    }
  };
  window.addEventListener("scroll", changeBg);
  return (
    <div>
      <nav className={nav ? "active" : " "}>
        <img src={logo} alt="logo" className="logo"></img>
        <ul>
          <li>
            <Link to="/">Return to main</Link>
          </li>
        </ul>
      </nav>
        <article className="rodo-container">
          <h1 style={{ textAlign: "center" }}>Obowizek informacyjny RODO</h1>
          <p>
            Ponisza informacja stanowi zwizy, zrozumiay i przejrzysty skrt
            informacji zamieszczonych w
            <a href="https://powerofnow.space/rodo">Polityce Prywatnoci</a>{" "}
            odnonie Administratora danych, celu i sposobu przetwarzania danych
            osobowych oraz Twoich praw w zwizku z tym przetwarzaniem, w formie
            wymaganej do spenienia obowizku informacyjnego RODO. Szczegy
            dotyczce sposobu przetwarzania i podmiotw uczestniczcych w tym
            procesie dostpne s we wskazanej polityce.
          </p>
          <h2>Kto jest administratorem danych?</h2>
          <p>
            Administratorem Danych Osobowych (dalej Administrator) jest firma
            "Tricity Solution Sp z o.o.", prowadzca dziaalno pod adresem:
            ul. LBORSKA 3B/--- 80-386 GDASK POMORSKIE, o nadanym numerze
            identyfikacji podatkowej (NIP): 5842813640, wiadczca usugi drog
            elektroniczn za porednictwem Serwisu
          </p>
          <h2>Jak mona skontaktowa si z administratorem danych?</h2>
          <p>
            Z Administratorem mona skontaktowa si w jeden z poniszych
            sposobw
          </p>
          <ul>
            <li>
              <p>
                <strong>Adres pocztowy</strong>- Tricity Solution Sp z o.o., ul.
                LBORSKA 3B/--- 80-386 GDASK POMORSKIE
              </p>
            </li>{" "}
            <li>
              <p>
                <strong>Formularz kontaktowy</strong> - dostpny pod adresem:
                https://powerofnow.space/#contact
              </p>
            </li>
          </ul>
          <h2>Czy Administrator powoa Inspektora Danych Osobowych?</h2>
          <p>
            Na podstawie Art. 37 RODO, Administrator nie powoa Inspektora
            Ochrony Danych.
          </p>
          <p>
            W sprawach dotyczcych przetwarzania danych, w tym danych osobowych,
            naley kontaktowa si bezporednio z Administratorem.
          </p>
          <h2>Skd pozyskujemy dane osobowe i jakie s ich rda?</h2>
          <p>Dane pozyskiwane s z nastpujcych rde:</p>
          <ul>
            <li>od osb, ktrych dane dotycz</li>
            <li>
              w przypadku rejestracji przy uyciu portali spoecznociowych, za
              wyraon wiadom zgod tych osb, z tych portali
              spoecznociowych
            </li>
          </ul>
          <h2>Jaki jest zakres przetwarzanych przez nas danych osobowych?</h2>
          <p>
            W serwisie przetwarzane s <strong>dane osobowe zwyke</strong>,
            podane dobrowolnie przez osoby, ktrych dotycz
            <br />
            <small>
              (Np. imi i nazwisko, login, adres e-mail, telefon, adres IP,
              itp.)
            </small>
          </p>
          <p>
            Szczegowy zakres przetwarzanych danych dostpny jest w
            <a href="https://powerofnow.space/rodo">Polityce Prywatnoci</a>.
          </p>
          <h2>Jakie s cele przetwarzania przez nas danych?</h2>
          <p>
            Dane osobowe dobrowolnie podane przez Uytkownikw s przetwarzane w
            jednym z nastpujcych celw:
          </p>
          <ul>
            <li>
              Realizacji usug elektronicznych:<ul></ul>
            </li>
            <li>
              Komunikacji Administratora z Uytkownikami w sprawach zwizanych z
              Serwisem oraz ochrony danych
            </li>
            <li>Zapewnienia prawnie uzasadnionego interesu Administratora</li>
          </ul>
          <h2>Jakie s podstawy prawne przetwarzania danych?</h2>
          <p>Serwis gromadzi i przetwarza dane Uytkownikw na podstawie:</p>
          <ul>
            <li>
              Rozporzdzenia Parlamentu Europejskiego i Rady (UE) 2016/679 z
              dnia 27 kwietnia 2016 r. w sprawie ochrony osb fizycznych w
              zwizku z przetwarzaniem danych osobowych i w sprawie swobodnego
              przepywu takich danych oraz uchylenia dyrektywy 95/46/WE (oglne
              rozporzdzenie o ochronie danych)
              <ul>
                <li>
                  art. 6 ust. 1 lit. a<br />
                  <small>
                    osoba, ktrej dane dotycz wyrazia zgod na przetwarzanie
                    swoich danych osobowych w jednym lub wikszej liczbie
                    okrelonych celw
                  </small>
                </li>
                <li>
                  art. 6 ust. 1 lit. b<br />
                  <small>
                    przetwarzanie jest niezbdne do wykonania umowy, ktrej
                    stron jest osoba, ktrej dane dotycz, lub do podjcia
                    dziaa na danie osoby, ktrej dane dotycz, przed
                    zawarciem umowy
                  </small>
                </li>
                <li>
                  art. 6 ust. 1 lit. f<br />
                  <small>
                    przetwarzanie jest niezbdne do celw wynikajcych z prawnie
                    uzasadnionych interesw realizowanych przez administratora
                    lub przez stron trzeci
                  </small>
                </li>
              </ul>
            </li>
            <li>
              Ustawa z dnia 10 maja 2018 r. o ochronie danych osobowych (Dz.U.
              2018 poz. 1000)
            </li>
            <li>
              Ustawa z dnia 16 lipca 2004 r. Prawo telekomunikacyjne (Dz.U. 2004
              nr 171 poz. 1800)
            </li>
            <li>
              Ustawa z dnia 4 lutego 1994 r. o prawie autorskim i prawach
              pokrewnych (Dz. U. 1994 Nr 24 poz. 83)
            </li>
          </ul>
          <h2>
            Jaki jest prawnie uzasadniony interes realizowany przez
            Administratora?
          </h2>
          <ul>
            <li>
              W celu ewentualnego ustalenia, dochodzenia lub obrony przed
              roszczeniami  podstaw prawn przetwarzania jest nasz uzasadniony
              interes (art. 6 ust. 1 lit. f) RODO) polegajcy na ochronie
              naszych praw, w tym midzy innymi;
            </li>
            <li>W celu oceny ryzyka potencjalnych klientw</li>
            <li>W celu oceny planowanych kampanii marketingowych</li>
            <li>W celu realizacji marketingu bezporedniego</li>
          </ul>
          <h2>Przez jaki okres przetwarzamy dane osobowe?</h2>
          <p>
            Co do zasady, wskazane dane osobowe s przechowywane wycznie przez
            okres wiadczenia usugi w ramach prowadzonego serwisu przez
            Administratora. S one usuwane lub anonimizowane w okresie do{" "}
            <strong>30 dni od chwili zakoczenia wiadczenia usug</strong> (np.
            usunicie zarejestrowanego konta uytkownika, wypisanie z listy
            Newsletter, itp.)
          </p>
          <p>
            W wyjtkowych sytuacjach, w celu zabezpieczenie prawnie
            uzasadnionego interesu realizowanego przez Administratora, okres ten
            moe ulec wydueniu. W takiej sytuacji Administrator bdzie
            przechowywa wskazane dane, od czasu dania ich usunicia przez
            Uytkownika, nie duej ni przez okres 3 lat w przypadku naruszenia
            lub podejrzenia naruszenia zapisw regulaminu serwisu przez osob,
            ktrej dane dotycz.
          </p>
          <h2>Kto jest odbiorc danych w tym danych osobowych?</h2>
          <p>Co do zasady jedynym odbiorc danych jest Administrator.</p>
          <p>
            Przetwarzanie danych moe jednak by powierzone innym podmiotom,
            realizujcym usugi na rzecz Administratora w celu utrzymania
            dziaalnoci Serwisu.
          </p>
          Do podmiotw takich mona zaliczy midzy innymi:
          <ul>
            <li>
              Firmy hostingowe, wiadczce usugi hostingu lub usug pokrewnych
              dla Administratora
            </li>
          </ul>
          <h2>
            Czy Pastwa dane osobowe bd przekazywane poza Uni Europejsk?
          </h2>
          <p>
            Dane osobowe nie bd przekazywane poza Uni Europejsk, chyba e
            zostay opublikowane na skutek indywidualnego dziaania Uytkownika
            (np. wprowadzenie komentarza lub wpisu), co sprawi, e dane bd
            dostpne dla kadej osoby odwiedzajcej serwis.
          </p>
          <h2>
            Czy dane osobowe bd podstaw zautomatyzowanego podejmowania
            decyzji?
          </h2>
          <p>
            Dane osobowe <strong>nie bd wykorzystywane</strong> do
            zautomatyzowanego podejmowania decyzji (profilowania).
          </p>
          <h2>
            Jakie maj Pastwo prawa zwizane z przetwarzaniem danych osobowych?
          </h2>
          <ul>
            <li>
              <p>
                <strong>Prawo dostpu do danych osobowych</strong>
                <br />
                Uytkownikom przysuguje prawo uzyskania dostpu do swoich
                danych osobowych, realizowane na danie zoone do
                Administratora
              </p>
            </li>
            <li>
              <p>
                <strong>Prawo do sprostowania danych osobowych</strong>
                <br />
                Uytkownikom przysuguje prawo dania od Administratora
                niezwocznego sprostowania danych osobowych, ktre s
                nieprawidowe lub / oraz uzupenienia niekompletnych danych
                osobowych, realizowane na danie zoone do Administratora
              </p>
            </li>
            <li>
              <p>
                <strong>Prawo do usunicia danych osobowych</strong>
                <br />
                Uytkownikom przysuguje prawo dania od Administratora
                niezwocznego usunicia danych osobowych, realizowane na danie
                zoone do Administratora. <br />
                <br />W przypadku kont uytkownikw, usunicie danych polega na
                anonimizacji danych umoliwiajcych identyfikacj Uytkownika.
                <br />
                <br />W przypadku usugi Newsletter, Uytkownik ma moliwo
                samodzielnego usunicia swoich danych osobowych korzystajc z
                odnonika umieszczonego w kadej przesyanej wiadomoci e-mail.
              </p>
            </li>
            <li>
              <p>
                <strong>
                  Prawo do ograniczenia przetwarzania danych osobowych
                </strong>
                <br />
                Uytkownikom przysuguje prawo ograniczenia przetwarzania danych
                osobowych w przypadkach wskazanych w art. 18 RODO, m.in.
                kwestionowania prawidowo danych osobowych, realizowane na
                danie zoone do Administratora
              </p>
            </li>
            <li>
              <p>
                <strong>Prawo do przenoszenia danych osobowych</strong>
                <br />
                Uytkownikom przysuguje prawo uzyskania od Administratora,
                danych osobowych dotyczcych Uytkownika w ustrukturyzowanym,
                powszechnie uywanym formacie nadajcym si do odczytu
                maszynowego, realizowane na danie zoone do Administratora
              </p>
            </li>
            <li>
              <p>
                <strong>
                  Prawo wniesienia sprzeciwu wobec przetwarzania danych
                  osobowych
                </strong>
                <br />
                Uytkownikom przysuguje prawo wniesienia sprzeciwu wobec
                przetwarzania jego danych osobowych w przypadkach okrelonych w
                art. 21 RODO, realizowane na danie zoone do Administratora
              </p>
            </li>
            <li>
              <p>
                <strong>Prawo wniesienia skargi</strong>
                <br />
                Uytkownikom przysuguje prawo wniesienia skargi do organu
                nadzorczego zajmujcego si ochron danych osobowych.
              </p>
            </li>
          </ul>
        </article>
    </div>
  );
}

export default Rodo;
